import React, { ReactNode, useCallback, useEffect, useRef, useState } from "react";
import "./QAToolTooltip.css"

const CLASS_NAME = "qa-tool-tooltip";

interface QAToolTooltipProps {
    className?: string,
    children: ReactNode,
    tooltipText: string
}

export const QAToolTooltip: React.FC<QAToolTooltipProps> = props => {
    const {className = "", children, tooltipText} = props;

    const [isTooltipShown, setIsTooltipShown] = useState(false);
    const timerRef = useRef<any>(null);

    const onMouseEnter = useCallback(() => {
        timerRef.current = setTimeout(() => setIsTooltipShown(true), 500);
    }, []);

    const onMouseLeave = useCallback(() => {
        clearTimeout(timerRef.current);
        setIsTooltipShown(false);
    }, []);

    // Do not show tooltip for an unmounted component
    useEffect(() => {
        return () => clearTimeout(timerRef.current);
    });

    return (
        <div
            className={`${CLASS_NAME} ${className}`}
            onMouseEnter={onMouseEnter}
            onMouseLeave={onMouseLeave}
        >
            {children}
            {isTooltipShown && <div className={`${CLASS_NAME}__content`}>{tooltipText}</div>}
        </div>
    );
};