import { confirmAlert } from "react-confirm-alert";

export const confirmAction = (text:string, confirmBtn: string, rejectBtn:string, onConfirm: () => void) => {
    confirmAlert({
        customUI: ({ onClose }) => (
            <div className='qa-tool-confirm-alert'>
                <h5>{text}</h5>
                <div className="qa-tool-confirm-alert__btns qa_tool__buttons-group">
                    <button className="btn btn-secondary" onClick={onClose}>{rejectBtn}</button>
                    <button
                        className="btn btn-success"
                        onClick={() => {
                            onConfirm();
                            onClose();
                        }}
                    >
                        {confirmBtn}
                    </button>
                </div>
            </div>
        )
    });
};