import "./QAToolContent.css"
import { QAManager } from "./QAManager";
import { useSelector } from "react-redux";
import { questionsCount } from "../store/questions/questionsSlice";

const CLASS_NAME = "qa-tool-content";

const getMessage = (count: number) : string => {
    const countOrNo = count > 0
        ? count
        : "no";
    const s = count === 1
        ? ""
        : "s";
    return `Here you can find ${countOrNo} question${s}. Feel free to create your own questions!`
}

export const QAToolContent: React.FC = () => {
    const count = useSelector(questionsCount);

    return (<div className={CLASS_NAME}>
        <div className={`${CLASS_NAME}__inner-container`}>
            <div className={`${CLASS_NAME}__info`}>{getMessage(count)}</div>
            <QAManager/>
        </div>
    </div>);
};