import React, { ReactNode, useCallback, useEffect, useRef, useState } from "react";
import { useSelector } from 'react-redux';
import SimpleBar from "simplebar-react";
import { QAItemEditMode } from "./QAItemEditMode";
import { QAItem } from "./QAItem";
import { addQuestion, addQuestionAsync, deleteAll, isQuestionListEmpty, questionsIds, sortQuestions } from '../store/questions/questionsSlice';
import { useAppDispatch } from '../store/hooks';
import { QAToolTooltip } from '../components/QAToolTooltip/QAToolTooltip';
import 'simplebar-react/dist/simplebar.min.css';
import "./QAManager.css"
import { confirmAction } from "./confirmAlertUtil";
import { useInfoPopup } from "../context/infoPopup";

const CLASS_NAME = "qa-manager";
const HEADER_TOOLTIP = "Here you can find the created questions and their answers.";

const getLi = (id: string, child: ReactNode) => (<li key={id} className="list-group-item">{child}</li>);

export const QAManager: React.FC = () => {
    const ids = useSelector(questionsIds);
    const isListEmpty = useSelector(isQuestionListEmpty);
    const [isNewOpen, setIsNewOpen] = useState(isListEmpty);
    const idsCount =useRef(ids.length);
    const { showInfoPopup } = useInfoPopup();

    const dispatch = useAppDispatch();
    const onCreateQuestion = useCallback((newQuestionText: string, newAnswer: string, isDelayActive: boolean) => {
        isDelayActive
            ? dispatch(addQuestionAsync({ question: newQuestionText, answer: newAnswer }))
            : dispatch(addQuestion({ question: newQuestionText, answer: newAnswer }));
    }, [dispatch]);
    const onCloseCreateNew = useCallback(() => setIsNewOpen(false), []);

    const onSort = () => {
        if (isListEmpty) {
            return;
        }
        confirmAction(
            "Are you sure you want to sort questions?",
            "Sort",
            "Cancel",
            () => {
                dispatch(sortQuestions());
                showInfoPopup("Questions are sorted!");
            }
        );
    }
    const onDeleteAll = () => {
        if (isListEmpty) {
            return;
        }
        confirmAction(
            "Are you sure you want to delete all questions?",
            "Delete all",
            "Cancel",
            () => dispatch(deleteAll())
        );
    };
    const onOpenCreateNew = () => setIsNewOpen(true);

    useEffect(() => {
        if(isListEmpty) {
            setIsNewOpen(true);
        }
    }, [isListEmpty]);

    const isTouchDevice = !!("ontouchstart" in document.documentElement);
    const ulRef = useRef<HTMLUListElement>(null);
    useEffect(() => {
        if(!isTouchDevice && idsCount.current < ids.length) {
            if(ulRef.current) {
                ulRef.current.children[ulRef.current.childNodes.length - 1].scrollIntoView();
            }
        }
        idsCount.current = ids.length;
    }, [ids, isTouchDevice]);

    const createNewRef = useRef<HTMLDivElement>(null);
    useEffect(() => {
        if (isNewOpen && createNewRef.current) {
            const createNewY = createNewRef.current.getBoundingClientRect().top;
            window.scroll(0, createNewY + window.scrollY);
        }
    }, [isNewOpen]);

    return (
        <div className={CLASS_NAME}>
            <QAToolTooltip className ={`${CLASS_NAME}__header-container`} tooltipText={HEADER_TOOLTIP}>
                <h3 className={`${CLASS_NAME}__header`}>Created questions</h3>
            </QAToolTooltip>
            {ids.length === 0 && <div className={`${CLASS_NAME}__warning `}>No questions yet :(</div>}
            <div className={`${CLASS_NAME}__simple-bar-container`}>
                <SimpleBar>
                    <ul className={`${CLASS_NAME}__list list-group`} ref={ulRef}>
                        {ids.map(
                            id => getLi(id, (<QAItem questionId={id}/>))
                        )}
                    </ul>
                </SimpleBar>
            </div>
            {isNewOpen && (
                <div className={`${CLASS_NAME}__create-new-container`} ref={createNewRef}>
                    <QAItemEditMode
                        onApply={onCreateQuestion}
                        onCancel={onCloseCreateNew}
                    />
                </div>)
            }
            <div className={`${CLASS_NAME}__buttons qa_tool__buttons-group`}>
                <button className="btn btn-secondary" onClick={onSort}>Sort</button>
                <button className={`${CLASS_NAME}__button-remove-all btn btn-secondary`} onClick={onDeleteAll}>Delete all</button>
                {!isNewOpen && (<button className={`${CLASS_NAME}__button-create-new btn btn-success`} onClick={onOpenCreateNew}>
                    <label>Create question</label>
                    <i className="bi bi-plus-lg"></i>
                </button>)}
            </div>
        </div>
    );
};