import React, { useCallback, useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { QAToolTooltip } from "../components/QAToolTooltip/QAToolTooltip";
import { isLoading } from "../store/questions/questionsSlice";
import { useInfoPopup } from "../context/infoPopup";
import "./QAItemEditMode.css";
import { QAToolCheckbox } from "../components/QAToolCheckbox/QAToolCheckbox";
const CLASS_NAME = "qa-item-edit-mode";
const EDIT_HEADER = "Edit the question";
const CREATE_HEADER = "Create a new question";
const CREATE_HEADER_TOOLTIP = "Here you can create new questions and their answers.";

interface QAItemEditModeProps {
    questionId?: string
    questionText?: string,
    answer?: string,
    onApply: (newQuestion: string, newAnswer: string, isDelayActive: boolean) => void,
    onCancel: () => void
}

const _QAItemEditMode: React.FC<QAItemEditModeProps> = props => {
    const {questionId, questionText = "", answer = "", onApply, onCancel} = props;

    const [newQuestion, setNewQuestion] = useState(questionText);
    const [newAnswer, setNewAnswer] = useState(answer);
    const [isValid, setIsValid] = useState(true);
    const [isDelayActive, setIsDelayActive] = useState(false);
    const loadingInProgress = useSelector(isLoading);
    const { showInfoPopup } = useInfoPopup();
    const addQuestionRequested = useRef(false);

    const onSaveHandler = () => {
        if(newQuestion === "") {
            setIsValid(false);
            return;
        }
        onApply(newQuestion, newAnswer, isDelayActive);
        if(!isDelayActive) {
            setNewQuestion("");
            setNewAnswer("");
            questionInputRef.current?.focus();
            showInfoPopup("Question is added!");
        } else {
            addQuestionRequested.current = true;
        }
    };
    const onUseDelayChanged = useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => setIsDelayActive(e.currentTarget.checked),
        []
    );
    const onQuestionChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
        setNewQuestion(e.currentTarget.value);
        setIsValid(true);
    };
    const onAnswerChanged = (e: React.ChangeEvent<HTMLTextAreaElement>) => setNewAnswer(e.currentTarget.value);

    const questionInputRef = useRef<HTMLInputElement>(null);
    useEffect(() => {
        questionInputRef.current?.focus();
    }, []);
    useEffect(() => {
        if(!questionId && !loadingInProgress) {
            setNewQuestion("");
            setNewAnswer("");
            questionInputRef.current?.focus();
            if(addQuestionRequested.current) {
                showInfoPopup("Question is added!");
            }
            addQuestionRequested.current = false;
        }
    }, [loadingInProgress, questionId, showInfoPopup]);

    const displayLoadingIndicator = loadingInProgress && !questionId;

    return (
        <div className={CLASS_NAME}>
            <div className={`${CLASS_NAME}__title`}>
                <div>
                    { questionId
                        ? <h5>{EDIT_HEADER}</h5>
                        : (
                            <QAToolTooltip tooltipText={CREATE_HEADER_TOOLTIP}>
                                <h3>{CREATE_HEADER}</h3>
                            </QAToolTooltip>)
                    }
                    <label className={`${CLASS_NAME}__question-label form-label`}>Question</label>
                </div>
                <div className={`${CLASS_NAME}__btns-checkbox`}>
                    <div className={`${CLASS_NAME}__buttons qa_tool__buttons-group`}>
                        <button
                            className={`btn btn-outline-success ${displayLoadingIndicator ? "" : "bi-check-lg"}`}
                            onClick={onSaveHandler}
                            disabled={displayLoadingIndicator} >
                                {displayLoadingIndicator && (<img className="loading-indicator" src="spinner.gif" alt="spinner"/>)}
                        </button>
                        <button
                            className="btn btn-outline-secondary bi-x-lg"
                            onClick={onCancel}
                            disabled={displayLoadingIndicator}>
                        </button>
                    </div>
                    { !questionId && <QAToolCheckbox
                        checked={isDelayActive}
                        disabled={displayLoadingIndicator}
                        label="Use delay"
                        onChange={onUseDelayChanged}/>
                    }
                </div>
            </div>
            <div className={`${CLASS_NAME}__inputs`}>
                <input
                    className={`form-control ${isValid ? "" : CLASS_NAME + "__question--invalid"}`}
                    ref={questionInputRef}
                    type="text"
                    value={newQuestion}
                    onChange={onQuestionChanged}
                    disabled={displayLoadingIndicator}
                />
                <label className="form-label">Answer</label>
                <textarea
                    className="form-control"
                    value={newAnswer}
                    onChange={onAnswerChanged}
                    disabled={displayLoadingIndicator}/>
            </div>
        </div>
    )
};

export const QAItemEditMode = React.memo(_QAItemEditMode);