import React from "react";
import "./QAToolCheckbox.css";

const CLASS_NAME = "qa-tool-checkbox"

interface QAToolCheckboxProps {
    checked: boolean,
    disabled: boolean,
    label: string,
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void
}

const _QAToolCheckbox: React.FC<QAToolCheckboxProps> = ({checked, disabled, label, onChange}) => (
        <div>
            <input
                className={`${CLASS_NAME}__input`}
                type="checkbox"
                id="checkboxId"
                checked={checked}
                onChange={onChange}
                disabled={disabled}/>
            <label
                className={`${CLASS_NAME}__label`}
                htmlFor="checkboxId">{label}</label>
        </div>
    );

export const QAToolCheckbox = React.memo(_QAToolCheckbox);