import { combineReducers, configureStore, Action, ThunkAction, Middleware } from '@reduxjs/toolkit';
import questionsReducer from './questions/questionsSlice';

const rootReducer = combineReducers({
  questions: questionsReducer
})

export const localStorageMiddleware: Middleware<{}, RootState> = store => next => action => {
  const result = next(action);
  if ( action.type?.startsWith('questions/') ) {
      const {data, ids} = store.getState().questions;
      localStorage.setItem('qa-tool-questions', JSON.stringify({data, ids}))
  }
  return result;
};

export const store = configureStore({
    reducer: rootReducer,
    middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(localStorageMiddleware)
});

export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof rootReducer>;
export type AppThunk<ReturnType = void> = ThunkAction<
    ReturnType,
    RootState,
    unknown,
    Action<string>
>;