import React, { useCallback, useState } from "react";
import { useAppDispatch, useAppSelector } from "../store/hooks";
import { deleteQuestion, questionById, updateQuestion } from "../store/questions/questionsSlice";
import { QAItemEditMode } from "./QAItemEditMode";
import "./QAItem.css";
import { QAItemViewMode } from "./QAItemViewMode";
import { confirmAction } from "./confirmAlertUtil";

const CLASS_NAME = "qa-item";

interface QuestionItemProps {
    questionId: string
}

const _QAItem: React.FC<QuestionItemProps> = props => {

    const { questionId } = props;
    const [isInEditMode, setIsInEditMode] = useState(false);

    const {question, answer} = useAppSelector(state => questionById(state, questionId)) || {};
    const dispatch = useAppDispatch();

    const onApply = useCallback((newQuestion: string, newAnswer: string) => {
        dispatch(updateQuestion({id: questionId, question: newQuestion, answer: newAnswer}));
        setIsInEditMode(false);
    }, [dispatch, questionId]);
    const onCancel = useCallback(() => setIsInEditMode(false), []);
    const onDelete = useCallback(() => {
        confirmAction(
            "Are you sure you want to delete the question?",
            "Delete",
            "Cancel",
            () => dispatch(deleteQuestion({id: questionId}))
        );
    }, [dispatch, questionId]);
    const onEdit = useCallback(() => setIsInEditMode(true), []);

    return (
        <div className={CLASS_NAME}>
            {isInEditMode
                ? <QAItemEditMode
                    questionId={questionId}
                    questionText={question}
                    answer={answer}
                    onApply={onApply}
                    onCancel={onCancel}/>
                : <QAItemViewMode
                    question={question}
                    answer={answer}
                    onDelete={onDelete}
                    onEdit={onEdit}
                    />
            }
        </div>
    );
};

export const QAItem = React.memo(_QAItem);