import React, { useState } from "react";
import "./QAItemViewMode.css"

const CLASS_NAME = "qa-item-view-mode";

interface QAItemViewModeProps {
    answer: string,
    question: string,
    onDelete: () => void,
    onEdit: () => void
};

const _QAItemViewMode: React.FC<QAItemViewModeProps> = props => {
    const {question, answer, onDelete, onEdit} = props;
    const [isAnswerOpen, setIsAnswerOpen] = useState(false);
    const onQuestionClick = () => setIsAnswerOpen(!isAnswerOpen);

    return (
        <div className={CLASS_NAME}>
            <div className={`${CLASS_NAME}__question-btns`}>
                <div
                    className={`${CLASS_NAME}__question ${isAnswerOpen ? CLASS_NAME + "__question--open" : ""}`}
                    onClick={onQuestionClick}>
                        <i className="bi bi-caret-right-fill"></i>
                        <h5>{question}</h5>
                </div>
                <div className="qa_tool__buttons-group">
                    <button className="btn btn-outline-secondary bi-pencil-fill" onClick={onEdit}></button>
                    <button className="btn btn-outline-secondary bi-trash3-fill " onClick={onDelete}></button>
                </div>
            </div>
            {isAnswerOpen && <div className={`${CLASS_NAME}__answer`}>{answer}</div>}
        </div>
    );
};

export const  QAItemViewMode = React.memo(_QAItemViewMode);