import { createAsyncThunk, createSelector, createSlice, nanoid, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../store";

interface Question {
    id: string,
    question: string,
    answer: string
};

interface QuestionWithoutId {
    question: string,
    answer: string
};

interface QuestionsState {
    ids: string[],
    data: { [key: string]: Question },
    status: "loading" | "idle" | "failed",
    error: "string" | null
};

const a: QuestionsState= {status: "idle", error: null, ids: ["1"], data:  {
    "1": {
        id: "1",
        question: "My best question",
        answer: "My best answer"
    }
}};

const localStorageItem = localStorage.getItem('qa-tool-questions');
if (!localStorageItem) {

    for(let i = 0; i< 20; i++) {
        const id = nanoid();
        a.ids.push(id)
        a.data[id] = {id, question: `Question ${i}`, answer: id};
    }
} else {
    const localStorageState: {data: { [key: string]: Question }, ids: string[],} = JSON.parse(localStorageItem);
    a.data = localStorageState.data;
    a.ids = localStorageState.ids;
}

const initialState: QuestionsState = a;

const delayedPromise = (cb: Function) =>
    new Promise(resolve => setTimeout(() => resolve(cb()), 1000));

export const addQuestionAsync = createAsyncThunk<void, QuestionWithoutId>(
    "questions/addQuestionAsync",
    async (payload: QuestionWithoutId, thunkAPI) => {
        await delayedPromise(() => thunkAPI.dispatch(addQuestion(payload)));
    }
);

const questionsSlice = createSlice({
    name: "questions",
    initialState,
    extraReducers: builder => {
        builder
          .addCase(addQuestionAsync.pending, (state) => {
            state.status = 'loading';
          })
          .addCase(addQuestionAsync.fulfilled, (state, action) => {
            state.status = 'idle';
          })
          .addCase(addQuestionAsync.rejected, (state) => {
            state.status = 'failed';
          });
    },
    reducers: {
        addQuestion: {
            reducer(state, action: PayloadAction<Question>) {
                const {id} = action.payload;
                if(state.data[id] || action.payload.question === "") {
                    return state;
                }
                state.ids.push(id);
                state.data[id] = action.payload;
            },
            prepare({question, answer}: QuestionWithoutId) {
                return {
                    payload: {
                        question,
                        answer,
                        id: nanoid()
                    }
                }
            }
        },
        deleteAll(state) {
            state.ids = [];
            state.data = {};
        },
        deleteQuestion(state, action: PayloadAction<{id: string}>) {
            const { id } = action.payload;
            if(state.data[id]) {
                delete state.data[id];
                const index = state.ids.findIndex(_id => _id === id)
                state.ids.splice(index, 1);
            }
        },
        sortQuestions(state) {
            state.ids.sort((id1, id2) => state.data[id1].question.localeCompare(state.data[id2].question));
        },
        updateQuestion(state, action: PayloadAction<Question>) {
            const { id } = action.payload;
            if(state.data[id]) {
                state.data[id] = action.payload;
            }
        }
    }
});

const selfSelector = (state: RootState) => state.questions;
export const questionsCount = createSelector(selfSelector, state => state.ids.length);
export const questionById = createSelector(selfSelector, (_: RootState,id: string) => id, (state, id) => state.data[id]);
export const questionsIds = createSelector(selfSelector, state => state.ids);
export const isQuestionListEmpty = createSelector(questionsIds, ids => !ids.length);
export const isLoading = createSelector(selfSelector, state => state.status === "loading");

export const {addQuestion, deleteAll, deleteQuestion, sortQuestions, updateQuestion} = questionsSlice.actions;
export default questionsSlice.reducer;