import React, { useCallback, useEffect, useState } from "react"

const InfoPopupContext = React.createContext<any>({});

interface infoPopupProps {
    isOpen: boolean
    text: string
}
const InfoPopup: React.FC<infoPopupProps> = props => {
    const {isOpen, text} = props;
    return (
        <div className={`info-popup ${isOpen ? "info-popup--open" : ""}`}>
            <div>
                {text}
            </div>
        </div>
      )
};

const InfoPopupProvider = (props: any) => {
    const [isPopupOpen, setIsPopupOpen] = useState(false);
    const [text, setText] = useState("");
    const showInfoPopup =  useCallback((text: string) => {
        setText(text);
        setIsPopupOpen(true);
    }, []);

    useEffect(() => {
        if(isPopupOpen) {
            setTimeout(() => {
                setIsPopupOpen(false);
            }, 1000);
        }
    }, [isPopupOpen]);
    return (
        <InfoPopupContext.Provider value={{showInfoPopup}} {...props}>
            {props.children}
            <InfoPopup isOpen={isPopupOpen} text={text} />
        </InfoPopupContext.Provider>
    )
};

const useInfoPopup = () => {
    const context = React.useContext(InfoPopupContext)
    if (context === undefined) {
        throw new Error('useQAToolTooltip must be used within a QAToolTooltipProvider')
    }
    return context
}

export { InfoPopupProvider, useInfoPopup }